/* Global styles
========================================================================== */

html {
  box-sizing: border-box;
  font-size: 62.5%; // 1rem = 10px, 3.5rem = 35px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  content: '';
  clear: both;
  height: 0;
}

.show {
  display: block;
}

.hide {
  display: none;
}

body {
  background: $background;
  font-size: 1.6rem;
  letter-spacing: -0.025rem;
}

img {
  display: block;
  max-width: 100%;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visuallyHidden:not(:focus):not(:active) { 
  position: absolute; 
  width: 1px; 
  height: 1px; 
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(00 0 0);
  overflow: hidden;
}

.gridWrapper {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 4% repeat(24, 1fr) 4%;
  grid-column-gap: 1%;
  @include respond-to('tablet') {
    grid-template-columns: 4% repeat(12, 1fr) 4%;
    grid-column-gap: 1%;
  }
  @include respond-to('mobile') {
    grid-template-columns: 3% repeat(8, 1fr) 3%;
    grid-column-gap: 2%;
  }
}

.btn {
  appearance: none;
  border: none;
  border-radius: 4px;
  color: $white;
  cursor: pointer;
  font-family: $font-heading;
  font-size: 2.1rem;
  padding: 1rem 2.4rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  text-decoration: underline;
  text-decoration-color: transparent;
  transition: background 0.3s ease, color 0.3s ease, text-decoration-color 0.3s ease;
  @include respond-to('mobile') {
    font-size: 1.6rem;
    padding: 1rem 1.6rem;
  }
}

.runTypeEasy::after {
  background-color: $easy;
}
.runTypeTempo::after {
  background-color: $tempo;
}
.runTypeIntervals::after {
  background-color: $interval;
}
.runTypeHills::after {
  background-color: $hill;
}
.runTypeRace::after {
  background-color: $race;
}
.runTypeDefault::after {
  background-color: $default;
}
.runTypeLong::after {
  background-color: $long;
}
.runTypeRecovery::after {
  background-color: $recovery;
}